

.header {
  position: relative;
  width: 100%;
  z-index: 3;
  // background: rgba(33, 33, 33, 0.5);
  // box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  @include vp-767 {
    background: rgba(33, 33, 33, 0.85);
  }

  &__wrapper {
    padding: 20px 0;
    display: flex;
    gap: 10px;
    justify-content: space-between;

    @include vp-767 {
      display: grid;
      grid-template-columns: 1fr 1fr;
      padding: 10px 0;
    }

    @include vp-389 {
      gap: 50px 10px;
    }
  }
  
  .header__logo {
    width: 225px;
    height: 100px;
    flex-shrink: 0;

    @include vp-767 {
      grid-column: 1;
      grid-row: 1;
    }

    @media screen and (max-width: 500px) {
      width: 150px;
      height: 70px;
    }
  }

  .main-nav {
    display: flex;

    @include vp-767 {
      grid-column: 1/4;
      margin: 0 auto;
      grid-row: 2;
    }
  }

  .main-nav__menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }

  .main-nav__list {
    display: flex;
    gap: 25px;
  }

  .main-nav__link {
    font-family: 'Raleway';
    font-size: 22px;
    line-height: 100%;
    color: rgba(255, 255, 255, 1);
    transition: .3s;

    &:hover {
      opacity: 0.6;
    }
  }

  .main-nav__menu-bottom {
    margin-top: auto;
    display: flex;
  }

  .main-nav__search {
    display: flex;
    gap: 4px;
    align-items: center;
  }

  .custom-input input{
    width: 520px;
    height: 40px;
    border-radius: 40px 0px 0 40px;
    background: #fff;
    color: black;
    padding: 20px 40px 20px 13px;
  }

  .custom-input input::placeholder{
    font-family: 'Raleway';
    font-size: 12px;
    font-weight: 700;
    line-height: 100%;
    color: rgba(4, 18, 11, 0.4);
  }

  .main-nav__search-icon {
    background: rgba(255, 116, 49, 1);
    padding: 8px 20px;
    border-radius: 0 40px 40px 0;
    display: flex;
    justify-content: center;
    transition: .3s;
    svg {
      transition: .3s;
      flex-shrink: 0;
    }

    &:hover {
      opacity: 0.6;
    }
  }

  &__social {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 36px;

    @include vp-767 {
      grid-column: 2;
      grid-row: 1;
      justify-self: end;
    }
  }

  &__lc {
    width: 32px;
    height: 32px;
    transition: .3s;
    svg {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      transition: .3s;
    }

    &:hover {
      opacity: 0.6;
    }

    @media screen and (max-width: 500px) {
      width: 22px;
      height: 22px;
    }
  }

  &.is-open .main-nav__toggle {
    background: transparent;
    border-radius: 2px;
  }
  
  &.is-open .main-nav {
    position: relative;
  }
  
  &.is-open .main-nav__toggle span:nth-child(1) {
    top: 17px;
    -webkit-transform: rotate(-47deg) translate(-5px, 11px);
    -ms-transform: rotate(-45deg) translate(0, -50%);
    transform: rotate(-45deg) translate(0px, 5px);
    background: rgb(255, 255, 255);
    height: 4px;
    width: 40px;
  }
  
  &.is-open .main-nav__toggle span:nth-child(2) {
    opacity: 0;
    visibility: hidden;
  }
  
  &.is-open .main-nav__toggle span:nth-child(3) {
    top: 25px;
    -webkit-transform: rotate(45deg) translate(0, 50%);
    -ms-transform: rotate(45deg) translate(0, 50%);
    transform: rotate(45deg) translate(0px, -7px);
    background: rgb(255, 255, 255);
    height: 4px;
    width: 40px;
  }
  
  &.is-open .main-nav__menu {
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
  }
  
  .main-nav__toggle {
    position: relative;
    display: none;
    // width: 40px;
    // height: 40px;
    padding: 0;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: opacity rgb(255, 255, 255);
    -o-transition: opacity rgb(255, 255, 255);
    transition: opacity rgb(255, 255, 255);
  }
  
  .main-nav__toggle span {
    display: block;
    position: absolute;
    left: 10px;
    height: 4px;
    width: 40px;
    border-radius: 2px;
    background-color: rgb(255, 255, 255);
    -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
    -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  }
  
  .main-nav__toggle span:nth-child(1) {
    top: 8px;
  }
  
  .main-nav__toggle span:nth-child(2) {
    top: 50%;
  
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  
  .main-nav__toggle span:nth-child(3) {
    bottom: 8px;
  }
  
  .main-nav__toggle.focus-visible:focus {
    opacity: 0.8;
  }
  


  @media (max-width: 1150px) {

    .main-nav {
      width: auto;
      display: flex;
      align-items: center;
     
    }

    .main-nav__toggle {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
    }

    .main-nav__menu {
      z-index: 10;
      position: fixed;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
      justify-content: flex-start;
      -webkit-box-align: start;
      -ms-flex-align: start;
      align-items: flex-start;
      overflow: auto;
      top: 140px;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      padding: 30px 40px 50px;
      background-color: rgb(236, 204, 158);
      -webkit-transition: visibility 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
      -o-transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
      transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease;
      transition: transform 0.3s ease, visibility 0.3s ease, opacity 0.3s ease, -webkit-transform 0.3s ease;
      -webkit-transform: translateX(100%);
      -ms-transform: translateX(100%);
      transform: translateX(100%);

      @include vp-1023 {
        padding: 30px 20px 50px;
      }

      @include vp-767 {
        top: 170px;
      }

      @media screen and (max-width: 500px) {
        top: 150px;
      }

      @include vp-389 {
        top: 190px;
      }
    }

    .main-nav__list,
    .main-nav__menu {
      flex-direction: column;
      align-items: flex-start;
      margin: 0;
      max-width: 100%;
    }

    .main-nav__menu-top {
      width: 100%;
    }

    .main-nav__list {
      margin-bottom: 100px;
    }

    // .main-nav__item.menu-item-has-children.is-active .main-nav__sublist,
    // .section__integrations ul {
    //   display: flex;
    //   flex-direction: column;
    //   gap: 15px;
    // }

    .main-nav__item {
      width: 100%;
    }

    .main-nav__menu-bottom {
      width: 100%;

      form {
        width: 100%;
      }
    }

    .main-nav__search {
      width: 100%;
    }

    .custom-input {
      width: 100%;
    }

    .custom-input input {
      width: 100%;
    }
  }

  

}