.lk {
    overflow: hidden;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }
    }

    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }

    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    &__link-left {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 900;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 115px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        justify-content: end;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -115px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 950px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }
    }

    &__icon {
        svg {
            width: 35px;
            height: 35px;
        }
    }

    &__bottom {
        margin-top: 30px;
    }

    &__about {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 30px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 10px;
        max-width: 370px;
    }

   

    .custom-input input {
        border-radius: 30px;
        padding: 20px;
        height: 50px;
        background-color: rgba(180, 209, 235, 1);
    }

    .custom-input input::placeholder {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);

        @include vp-389 {
            font-size: 11px;
        }
    }

    .custom-select {
        max-width: 100%;
        position: relative;
    }

    .btn-reset.custom-select__button {
        border-radius: 30px;
        padding: 20px;
        height: 50px;
        background-color: rgba(180, 209, 235, 1);
    }

    .custom-select__text {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);
        text-align: left;
        @include vp-389 {
            font-size: 11px;
        }
    }

    .custom-select__icon {
        width: 14px;
        height: 18px;
        clip-path: none;
        background: none;
        svg {
            width: 14px;
            height: 12px;
        }
    }

    .custom-select__list {
        background: rgba(254, 255, 255, 1);
        border-radius: 20px;
        height: auto;
        padding: 15px 20px;
        display: none;
    }

    .custom-select.is-open .custom-select__list {
        display: block;
    }

    .custom-select__item {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(142, 142, 147, 1);

        
    }

}