.cancel {
    &__wrapper {
        max-width: 400px;
        width: 100%;
        margin: 0 auto;
        display: block;
        padding: 200px 0;

        span {
            
            font-weight: 700;
            font-style: Bold;
            font-size: 18px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            display: block;
            margin: 0 0 10px;
        }

        ol {
            list-style-type: decimal;
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-left: 20px;
            margin-bottom: 20px;
        }

        li {
            list-style-type: decimal;
    
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
        }
    }

    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 24px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        margin: 0 0 30px;
        max-width: 400px;
    }

    &__text {
        
        font-weight: 800;
        font-style: ExtraBold;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0 0 40px;
    }

    &__buttons {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__btn {
        max-width: 100%;
        width: 100%;
        border-radius: 60px;
        background: rgba(0, 0, 0, 1);
        padding: 13px;
        color: rgba(255, 255, 255, 1);
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: .3s;

        &--white {
            background-color: rgba(254, 255, 255, 1);
        
            font-weight: 500;
            font-style: Medium;
            font-size: 14px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            text-align: center;
            color: rgba(143, 140, 140, 1);
        }
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__element {
        span {
            
            font-weight: 500;
            font-style: Medium;
            font-size: 14px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            color: rgba(0, 0, 0, 1);
            display: block;
            margin-bottom: 10px;
        }
    }
}