.first1 {
    overflow-x: hidden;
    overflow-y: auto;
    &__wrapper {
        display: flex;
        min-height: 100vh;
    }

    &__left {
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: -100vw; // Сдвигаем далеко влево
          width: calc(100% + 100vw); // Занимаем всю ширину + сдвиг
          height: 100%;
          background: rgba(107, 161, 218, 0.55);
          z-index: -1;
        }

        @media screen and (max-width: 790px) {
            width: 25%;
            flex-shrink: 0;    
        
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__left-btn {
        max-width: 155px;
        width: 100%;
        background: rgba(254, 255, 255, 1);
        border-radius: 60px;
        padding: 8px;
    
        font-weight: 600;
        font-style: SemiBold;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        display: flex;
        flex-direction: row-reverse;
        gap: 10px;
        align-items: center;
        justify-content: center;
        transition: .3s;

        svg {
            width: 24px;
            height: 24px;
            flex-shrink: 0;

            @media screen and (max-width: 500px) {
                display: none;
            }
        }

        @include vp-767 {
            font-size: 12px;
        }

        
        @media screen and (max-width: 630px) {
            width: 118px;
        }

        @media screen and (max-width: 500px) {
            width: 100%;
        }

        @media screen and (max-width: 440px) {
            padding: 4px;

        }
    }

    &__profile {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        gap: 14px;
        margin-top: auto;
    
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;

        @include vp-767 {
            font-size: 14px;
        }

        @media screen and (max-width: 630px) {
            gap: 5px;
            flex-direction: column-reverse;
        }
    }

    &__right {
        width: 70%;
        padding: 30px 0 30px 100px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 44px;
    

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -120px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 950px) {
                left: -30px;
            }
        }
    }



    &__top-text {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 20px;
    }

    &__top-icons {
        display: flex;
        align-items: center;
        gap: 100px;
        position: relative;
        max-width: 525px;
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 12.5px;
            right: 12.5px; /* Убираем calc и gap */
            height: 1px;
            background: rgba(255, 198, 193, 1);
            transform: translateY(-50%);
            z-index: 0;
        }
      
        svg {
          width: 25px;
          height: 25px;
          flex-shrink: 0;
          position: relative;
          z-index: 1;
        }

        @include vp-767 {
            gap: 50px;
            max-width: 330px;
        }

        @media screen and (max-width: 500px) {
            gap: 20px;
            max-width: 215px;
        }
    }

    &__bottom {
        padding: 100px 0 0;
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__title {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: start;
        gap: 20px;

        
        font-weight: 700;
        font-style: Bold;
        font-size: 30px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(29, 29, 31, 1);
        margin: 0 0 32px;
    }

    &__text {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 22px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        color: rgba(29, 29, 31, 1);
        margin: 0 0 20px;
    }

    &__desc {
    
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 20px;
    }

    &__bottom-links {
        display: flex;
        gap: 15px;
        align-items: center;
        margin-bottom: 30px;

        @include vp-767 {
            flex-direction: column;
            align-items: start;
        }
    }

    &__link {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 13px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        border: 1px solid rgba(180, 209, 235, 1);
        padding: 10px 20px;
        border-radius: 100px;
        color: rgba(255, 255, 255, 1);
        background: rgb(114, 182, 243);
        transition: .3s;

        @include vp-767 {
            width: 100%;
        }
    }

    &__link:hover {
        opacity: 0.8;
    }

    form {
        margin-top: auto;
    }

    .chat-message-with-attachment {
        min-height: 114px;
        background: rgba(180, 209, 235, 1);
        border-radius: 20px;
        padding: 20px;
        position: relative;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        gap: 15px;
    }

    .message-text {
        max-width: 500px;
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0;
        color: rgba(58, 58, 58, 1);

        @media screen and (max-width: 950px) {
            max-width: 420px;
        }

        @include vp-767 {
            max-width: 350px;
        }

        @media screen and (max-width: 650px) {
            max-width: 250px;
        }

        @media screen and (max-width: 520px) {
            max-width: 190px;
        }

        
        @media screen and (max-width: 450px) {
            max-width: 140px;
        }

        @include vp-389 {
            max-width: 100px;
        }
    }

    &__buttons {
        align-self: flex-end;
        display: flex;
        gap: 5px;

        @include vp-389 {
            flex-direction: column;
        }
    }

    .attach-button {
        // position: absolute;
        // right: 20px;
        // bottom: 15px;
        transition: .3s;
        svg {
            width: 36px;
            height: 36px;
            flex-shrink: 0;
            transition: .3s;
        }
    }

    .attach-button:hover {
        svg {
            opacity: 0.8;
        }
    }

    &__btn {
        max-width: 100%;
        width: 100%;
        border-radius: 60px;
        background: rgba(0, 0, 0, 1);
        padding: 13px;
        color: rgba(255, 255, 255, 1);
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: .3s;
        margin-bottom: 10px;
    }

    &__btn:hover {
        opacity: 0.8;
    }
}