.how {
    overflow-x: hidden;
    overflow-y: auto;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }
    }

    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }

    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    &__link-left {
    
        font-weight: 400;
        font-style: Regular;
        font-size: 18px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 900;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 115px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        justify-content: end;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -115px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 950px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__user {
        svg {
            width: 31px;
            height: 32px;
        }

        &--text {
            
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin-left: 10px;
        }
    }

    &__bottom {
        margin-top: 30px;
    }

 

    &__tone {
        
        font-weight: 800;
        font-style: ExtraBold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 20px;
    }

    &__bottom-content {
        span {
            
            font-weight: 700;
            font-style: Bold;
            font-size: 18px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            display: block;
            color: rgba(58, 58, 58, 1);
            margin-bottom: 10px;
            margin-top: 20px;
        }

        ul {
            display: flex;
            flex-direction: column;
            gap: 10px;
            list-style: disc;
            padding-left: 20px;
            margin-bottom: 20px;
        }

        li {
            
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            list-style: disc;
        }
    }

    &__tsmal {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 0;
    }

    &__big-text {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 22px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        margin: 50px 0 0;

        @include vp-767 {
            font-size: 16px;
        }
    }

    .accordion-toggle {
        display: none;
    }

    @media (max-width: 768px) {
      
        .accordion-header {
            cursor: pointer;
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;

            font-size: 16px;

            @include vp-389 {
                font-size: 14px;
            }
        }

        .accordion-toggle {
            display: block;
            font-size: 30px;
            flex-shrink: 0;
            transition: transform 0.3s ease;

            @include vp-389 {
                font-size: 20px;
            }
        }

        .accordion-header.how__tone.active .accordion-toggle{
            transform: rotate(45deg);
        }
      
        .accordion-content {
          max-height: 0;
          overflow: hidden;
          transition: max-height 0.3s ease;
          
          &.show {
            max-height: 100%; 
            position: static;
            overflow: visible;
            opacity: 1;
          }

          span {
            margin-top: 0;
            font-size: 16px;

            @include vp-389 {
                font-size: 13px;
            }
          }

          .how__tsmal {
            font-size: 16px;
            margin-bottom: 10px;

            @include vp-389 {
                font-size: 13px;
            }
          }

          li {
            font-size: 16px;

            @include vp-389 {
                font-size: 13px;
            }
          }
        }
      
        
        .accordion-content:not(.show) {
          position: absolute;
          overflow: hidden;
          opacity: 0;
        }
    }
  
}