.login {

    
    
    &__wrapper {
        width: 100%;
        margin: 0 auto;
        display: block;
        padding: 70px 0;
    }

    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 40px;
        leading-trim: CAP_HEIGHT;
        line-height: 62.5px;
        letter-spacing: 0px;
        text-align: center;
        vertical-align: middle;
        margin: 0 0 30px;
        color: rgba(255, 255, 255, 1);
    }

    &__form {
        overflow: hidden;
        background: rgba(180, 209, 235, 1);
        max-width: 460px;
        width: 100%;
        border-radius: 30px;
        margin: 0 auto;
        padding: 30px;
    }

    &__text {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 20px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 20px;
    }

    &__quest {
    
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        margin: 0 0 30px;
        color: rgba(58, 58, 58, 1);
    }

    &__top {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 30px;
    }

    .custom-input {
        background: rgba(254, 255, 255, 1);
        padding: 12px 45px 12px 20px;
        border-radius: 30px;
        position: relative;
    }

    .custom-input input {
        padding: 0;
        color: black;
    }

    .custom-input__toggle.js-toggle-password {
        position: absolute;
        right: -25px;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        cursor: pointer;
    }

    .custom-checkbox {
        margin-bottom: 15px;
    }

    .custom-checkbox__icon {
        position: relative;
        /* ваши текущие стили */
    }
      
    .custom-checkbox__icon::after {
        content: "";
        position: absolute;
        top: 3px;
        left: 6px;
        width: 6px;
        height: 12px;
        border: solid #000; /* цвет галочки */
        border-width: 0 2px 2px 0;
        opacity: 0;
        transform: rotate(45deg);
        transition: opacity 0.3s ease;
    }
      
    input:checked + .custom-checkbox__icon::after {
        opacity: 1;
    }

    .custom-checkbox__text {
        font-size: 16px;
    }

    &__btn {
        max-width: 100%;
        width: 100%;
        border-radius: 60px;
        background: rgba(0, 0, 0, 1);
        padding: 13px;
        color: rgba(255, 255, 255, 1);
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: .3s;
    }

    &__btn:hover {
        opacity: 0.8;
    }
      
}