.full-modal {
    overflow-x: hidden;
    overflow-y: auto;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }
    }

    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }

    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    &__link-left {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 900;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 120px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        justify-content: end;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -115px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 950px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__user {
        svg {
            width: 31px;
            height: 32px;
        }

        &--text {
            
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin-left: 10px;
        }
    }

    &__bottom {
        margin-top: 30px;
    }

    &__title {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
    }

    &__bottom {
        ul {
            display: flex;
            flex-direction: column;
            gap: 30px;
        }

        li {
            display: grid;
            grid-template-columns: 2fr 1fr 1fr;
            gap: 15px;

            @media screen and (max-width: 820px) {
                grid-template-columns: 1fr 1fr 1fr;
            }

            @media screen and (max-width: 650px) {
                grid-template-columns: 1fr;
                gap: 5px;
            }
        }
    }

    &__name {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0;
        color: rgba(49, 119, 188, 1);
    }

    &__pus {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(0, 0, 0, 1);
        margin: 0;

        span {
            font-family: DM Sans;
            font-weight: 400;
            font-style: Regular;
            font-size: 10px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            display: block;

        }
    }

    &__change {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(255, 255, 255, 1);
        margin: 0;
        text-decoration: none;
    }
}

.full-modal {
    position: relative;
}

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    
    body.modal-open & {
        overflow: hidden;
    }
}

.modal-container {
    width: 460px;
    background: rgba(189, 221, 255, 1);
    border-radius: 30px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
    padding: 30px;
    position: relative;
    max-height: 90vh;
    // overflow-y: auto;
}

.modal-close {
    position: absolute;
    top: 15px;
    right: 15px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #999;
    
    &:hover {
        color: #333;
    }
}

.modal-title {
    
    font-weight: 700;
    font-style: Bold;
    font-size: 22px;
    leading-trim: CAP_HEIGHT;
    line-height: 120%;
    letter-spacing: 0%;
    text-align: center;
    color: rgba(29, 29, 31, 1);
    margin: 0 0 30px;
}

.modal-form {
    display: flex;
    flex-direction: column;
    gap: 5px;
    
    
    input {
        width: 100%;
        padding: 12px 15px;
        background: rgba(254, 255, 255, 1);
        border-radius: 30px;
        font-size: 16px;
        color: rgba(0, 0, 0, 1);
    }


    input::placeholder {
       
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(143, 140, 140, 1);
    }

    .modal-btn {
        max-width: 100%;
        width: 100%;
        border-radius: 60px;
        background: rgba(0, 0, 0, 1);
        padding: 13px;
        color: rgba(255, 255, 255, 1);
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: .3s;
        margin-top: 20px;
    }

    .modal-btn:hover {
        opacity: 0.8;
    }
}

