.tarif1 {
    overflow-x: hidden;
    overflow-y: auto;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }
    }

    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }

    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    &__link-left {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 900;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 120px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 1130px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        justify-content: end;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -120px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 1130px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__user {
        svg {
            width: 31px;
            height: 32px;
        }

        &--text {
            
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin-left: 10px;
        }
    }

    &__bottom {
        margin-top: 30px;
    }

    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 24px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        margin: 0 0 30px;
    }

    &__text {
    
        font-weight: 800;
        font-style: ExtraBold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 0 0 20px;
    }

    &__bottom {
        span {
            
            font-weight: 700;
            font-style: Bold;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            display: block;
            margin: 0 0 10px;
        }

        ul {
            list-style: disc;
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-left: 20px;
            margin-bottom: 20px;
        }

        li {
            list-style: disc;
            
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;

        }
    }

    

    

    &__text1 {
        
        font-weight: 600;
        font-style: Semibold;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        max-width: 450px;
        margin: 0 0 30px;
    }






    &__top-icons {
        display: flex;
        align-items: center;
        gap: 100px;
        position: relative;
        max-width: 525px;
        margin-bottom: 50px;
        &::before {
            content: '';
            position: absolute;
            top: 50%;
            left: 12.5px;
            right: 12.5px; /* Убираем calc и gap */
            height: 1px;
            background: rgba(255, 198, 193, 1);
            transform: translateY(-50%);
            z-index: 0;
        }
      
        svg {
          width: 25px;
          height: 25px;
          flex-shrink: 0;
          position: relative;
          z-index: 1;
        }

        @media screen and (max-width: 950px) {
            gap: 50px;
            max-width: 330px;
        }

        @media screen and (max-width: 630px) {
            gap: 20px;
            max-width: 215px;
        }

        @media screen and (max-width: 430px) {
            gap: 10px;
            max-width: 160px;
        }
    }


    

    &__card-content {
        border: 1px solid rgba(107, 161, 218, 1);
        border-radius: 30px;
        background: rgba(180, 209, 235, 1);
        padding: 20px;
        
        @media screen and (max-width: 500px) {
            padding: 10px;
        }
    }

    &__name {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 15px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        color: rgba(49, 119, 188, 1);
        margin: 0 0 15px;

        @media screen and (max-width: 500px) {
            font-size: 12px;
        }
    }

    &__desc {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 25px;
        color: rgba(49, 119, 188, 1);

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__price {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 28px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(29, 29, 31, 1);
        margin: 0 0 25px;

        @media screen and (max-width: 500px) {
            font-size: 20px;
        }
    }

    &__what {
    
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 25px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__card {
        max-width: 360px;
        ul {
            list-style: disc;
            display: flex;
            flex-direction: column;
            gap: 11px;
            padding-left: 20px;
            margin-bottom: 25px;
        }

        li {
            list-style: disc;
            
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
        }

        span {
            
            font-weight: 500;
            font-style: Italic;
            font-size: 14px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            text-align: center;
            color: rgba(58, 58, 58, 1);
            display: block;
            margin: 0 auto 15px;
        }

       
    }

    &__pod {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 15px 0 0;
        display: block;
        text-align: center;
    }

    &__card-content {
        &--white {
            background: rgba(254, 255, 255, 1);
            position: relative;
            border: none;
            .tarif1__image {
                position: absolute;
                width: 83px;
                height: 80px;
                top: -20px;
                right: -20px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }

    &__text-bottom-small {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 14px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        margin: 50px 0 0;
    }
}