.confirm {
    &__wrapper {
        max-width: 440px;
        width: 100%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 200px 0;

        svg {
            width: 62px;
            height: 62px;
            margin-bottom: 44px;
        }
    }


    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 22px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        margin: 0 0 30px;        
    }

    &__text {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0;
    }

    
}