.intro {
    position: relative;
    z-index: 2;
    @include vp-1279 {
        background: rgba(33, 33, 33, 0.2);
    }

    @include vp-767 {
        background: rgba(33, 33, 33, 0.5);
    }

    &__wrapper {
        
        z-index: 2;
        padding: 140px 0 50px;
        display: flex;
        flex-direction: column;
        justify-content: end;
        align-items: end;
        gap: 32px;

        @include vp-767 {
            align-items: center;
            justify-content: center;
            padding: 80px 0 30px;
        }

        @media screen and (max-width: 500px) {
            padding: 30px 0;
            gap: 20px;
        }
    }

    &__title {
        font-family: 'Comfortaa';
        font-size: 52px;
        font-weight: 700;
        line-height: 100%;
        color: rgba(70, 42, 27, 1);
        margin: 0;
        text-align: right;

        @include vp-1279 {
            color: rgba(255, 255, 255, 1);
        }

        @include vp-767 {
            text-align: center;
            font-size: 30px;
        }

        @media screen and (max-width: 500px) {
            font-size: 20px;
        }
    }

    &__text {
        font-family: 'Raleway';
        font-size: 22px;
        line-height: 32px;
        color: rgba(70, 42, 27, 1);
        margin: 0;
        text-align: right;
        max-width: 670px;
        @include vp-1279 {
            color: rgba(255, 255, 255, 1);
        }

        @include vp-767 {
            text-align: center;
            font-size: 22px;
        }

        @media screen and (max-width: 500px) {
            font-size: 16px;
            line-height: 22px;
        }
    }

    &__link {
        box-sizing: border-box;
        max-width: 286px;
        width: 100%;
        background: rgba(255, 116, 49, 1);
        font-family: 'Raleway';
        font-size: 22px;
        line-height: 100%;
        font-weight: 600;
        color: rgba(255, 255, 255, 1);
        border-radius: 20px;
        padding: 22px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 5px;
        box-shadow: 8px 8px 12px rgba(0, 0, 0, 0.25);
        transition: .3s;
        svg {
            width: 35px;
            height: 35px;
            transition: .3s;
            flex-shrink: 0;
        }

        &:hover {
            opacity: 0.8;

            svg {
                transform: rotate(45deg);
            }
        }

        @include vp-767 {
            padding: 10px 0;
            border-radius: 50px;
            font-size: 18px;
            box-shadow: none;
        }

        @media screen and (max-width: 500px) {
            font-size: 14px;
            max-width: 200px;
        }
    }
}