.custom-checkbox {
  position: relative;
  margin: 0 auto;
  text-align: center;
  label {
    display: inline-flex;
    align-items: center;

    cursor: pointer;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    width: 20px;
    height: 20px;
    margin-right: 0px;

    border-radius: 2px;
    background-color: transparent;

    transition: background-color $trans-default;
    border: 1px solid rgba(148, 162, 171, 1);
    img {
      flex-shrink: 0;

      width: 14px;
      height: 14px;

      transition: visibility $trans-default, opacity $trans-default;

      visibility: hidden;
      opacity: 0;
    }
  }

  input {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;

    clip: rect(0 0 0 0);
  }

  input:checked + .custom-checkbox__icon {

    img {
      visibility: visible;
      opacity: 1;
    }
  }

  input:checked ~ .custom-checkbox__text {
    opacity: 1;
    visibility: visible;
  }

  &__label {
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;

    user-select: none;

    @include vp-767 {
      font-size: 22px;
      line-height: 33px;
    }
  }

  &__text {
    margin: 4px 0 0;
    padding-left: 11px;
    font-family: DM Sans;
    font-weight: 500;
    font-style: Medium;
    font-size: 15px;
    leading-trim: CAP_HEIGHT;
    line-height: 130%;
    letter-spacing: 0px;


    color: rgba(39, 39, 39, 1);

    transition: opacity $trans-default, visibility $trans-default;

    // opacity: 0;
    // visibility: hidden;
  }
}
