.mac {
    &__wrapper {
        padding: 70px 0;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 15px;
        max-width: 700px;
        margin-bottom: 100px;
    }

    &__plan,
    &__amount,
    &__length,
    &__text {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 22px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        color: rgba(58, 58, 58, 1);
        margin: 0;

        span {
            font-weight: 700;
        }
    }

    &__card {
        width: 337px;
        height: 662px;
        margin: 0 auto;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @include vp-389 {
            width: 300px;
            height: 590px;
        }
    }
}