.phone {
    display: none;
    background: transparent;

    &__wrapper {
        display: flex;
        flex-direction: column;
        min-height: 100vh;
    }

    &__top {
        padding: 15px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
    }

    &__close {
        position: absolute;
        right: 15px;
        top: 25px;

        svg {
            width: 25px;
            height: 25px;
        }
    }

    @media screen and (max-width: 550px) {
        display: block;
    }

    .main-nav__toggle span {
        background: rgba(0, 0, 0, 1);
        width: 22px;
        height: 3px;
    }

    .main-nav__toggle span:nth-child(1) {
        width: 15px;
        top: 7px;
        height: 3px;
    }

    &__menu {
        display: flex;
        width: 80%;
        transform: translateX(-100%);
        background: rgba(107, 161, 218, 1);
        z-index: 10;
        position: fixed;
        left: 0;
        top: 0;
        height: 100vh;
        overflow: auto;
        transition: .3s;
    }


    &__left {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px;
    }

    &__label {
        
        
        font-weight: 590;
        font-style: Semibold;
        font-size: 17px;
        leading-trim: NONE;
        line-height: 22px;
        letter-spacing: 0px;
        text-align: center;
        vertical-align: middle;
        margin: 0;
        color: rgba(0, 0, 0, 1);
        
    }

    &__label-menu {
       
        font-weight: 600;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;
    }

    #edit {
        svg {
            width: 24px;
            height: 24px;
            flex-shrink: 0
        }
    }

    &__left-btn {
        max-width: 155px;
        width: 100%;
        background: rgba(254, 255, 255, 1);
        border-radius: 60px;
        padding: 8px;
    
        font-weight: 600;
        font-style: SemiBold;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        display: flex;
        flex-direction: row-reverse;
        gap: 10px;
        align-items: center;
        justify-content: center;
        transition: .3s;

        svg {
            width: 24px;
            height: 24px;
            flex-shrink: 0;

            @media screen and (max-width: 500px) {
                display: none;
            }
        }

        @include vp-767 {
            font-size: 12px;
        }

        
        @media screen and (max-width: 630px) {
            width: 118px;
        }

        @media screen and (max-width: 500px) {
            width: 100%;
        }

        @media screen and (max-width: 440px) {
            padding: 4px;

        }
    }

    &__profile {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        gap: 14px;
        margin-top: auto;
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;

        @include vp-767 {
            font-size: 14px;
        }

        @media screen and (max-width: 630px) {
            gap: 5px;
            flex-direction: column-reverse;
        }
    }

    &__chats {
        display: flex;
        flex-direction: column;
        align-items: self-start;
        width: 100%;
        margin-top: 15px;
        p {
            
            font-weight: 700;
            font-style: Bold;
            font-size: 18px;
            leading-trim: CAP_HEIGHT;
            line-height: 120%;
            letter-spacing: 0%;
            color: rgba(29, 29, 31, 1);
            margin: 0 0 15px;
        }

        span {
    
            font-weight: 400;
            font-style: Regular;
            font-size: 14px;
            leading-trim: CAP_HEIGHT;
            line-height: 120%;
            letter-spacing: 0%;
            display: block;
            margin-bottom: 10px;
            color: rgba(58, 58, 58, 1);
        }
    }

    &__user {
        display: flex;
        align-items: center;
        gap: 10px;
        background: rgba(180, 209, 235, 1);
        border-radius: 10px;
        padding: 5px;
        margin-bottom: 10px;
        width: 100%;
        img {
            width: 30px;
            height: 30px;
            object-fit: cover;

            @media screen and (max-width: 500px) {
                width: 25px;
                height: 25px;
            }
        }

        p {
            
            font-weight: 600;
            font-style: SemiBold;
            font-size: 14px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin: 0;

            @media screen and (max-width: 500px) {
                font-size: 12px;
            }
        }
    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__right {
        padding: 60px 0 30px 70px;
        display: flex;
        flex-direction: column;
        width: 70%;
        @media screen and (max-width: 950px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__form {
        display: flex;
        align-items: center;
        gap: 10px;

        @media screen and (max-width: 800px) {
            flex-direction: column;
            align-items: flex-start;
        }
        form {
            position: relative;
            input {
                background-color: rgba(254, 255, 255, 1);
                border-radius: 60px;
                padding: 14px;
                padding-left: 45px;
                border: none;
                width: 190px;

                @media screen and (max-width: 950px) {
                    width: 100%;
                }
            }

            input::placeholder {
                padding-left: 0px;
            
                font-weight: 600;
                font-style: SemiBold;
                font-size: 16px;
                leading-trim: NONE;
                line-height: 110.00000000000001%;
                letter-spacing: 0px;
                color: rgba(58, 58, 58, 1);
                
                @media screen and (max-width: 600px) {
                    font-size: 0;
                }
                
            }

            button {
                position: absolute;
                top: 55%;
                transform: translateY(-50%);
                left: 18px;

                svg {
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }

    &__left-bottom {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-top: auto;
        img {
            width: 40px;
            height: 40px;
            object-fit: cover;
        }

        p {
        
            font-weight: 600;
            font-style: SemiBold;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin: 0;
        }
    }

    &__bottom {
        display: flex;
        margin-top: auto;
        margin-bottom: 15px;
    }

    &__bottom-blocks {
        display: flex;
        gap: 15px;
    }

    &__bottom-item {
        display: flex;
        flex-direction: column;
        gap: 2px;
        background: rgba(180, 209, 235, 1);
        padding: 13px 16px;
        border-radius: 14px;
        p {
            
            font-weight: 590;
            font-style: Semibold;
            font-size: 16px;
            leading-trim: NONE;
            line-height: 21px;
            letter-spacing: -0.4px;
            vertical-align: middle;
            color: rgba(0, 0, 0, 1);
            margin: 0;

            @include vp-389 {
                font-size: 14px;
            }
        }

        span {
        
            font-weight: 400;
            font-size: 16px;
            leading-trim: NONE;
            line-height: 21px;
            letter-spacing: -0.4px;
            vertical-align: middle;
            color: rgba(60, 60, 67, 0.6);

            @include vp-389 {
                font-size: 14px;
            }
        }
    }

    &__form-content {
        display: flex;
        align-items: center;
        gap: 16px;
        position: relative;
        background: rgba(180, 209, 235, 1);
        border: 1px solid rgba(229, 229, 234, 1);
        border-radius: 32px;
        padding: 13px 13px 13px 9px;
        margin-bottom: 32px;
        max-height: 200px;

        @include vp-389 {
            gap: 10px;
        }
    }

    .custom-textarea {
        width: 100%;
        flex-grow: 1;
    }

    .custom-textarea textarea {
        padding: 0;
        color: black;
        height: 25px; 
        max-height: 200px; 
        resize: none; 
        overflow-y: auto; 
    }

    .custom-textarea textarea::placeholder {
        
        font-weight: 400;
        font-size: 17px;
        leading-trim: NONE;
        line-height: 22px;
        letter-spacing: 0px;
        color: rgba(0, 0, 0, 1);
    }

    &__button-menu {
        position: relative;
    }

    &__dop-menu {
        position: absolute;
        display: flex;
        gap: 20px;
        flex-direction: column;
        background: rgba(180, 209, 235, 1);
        padding: 10px;
        border-radius: 30px;
        border: 1px solid rgba(229, 229, 234, 1);
        z-index: 1;
        left: -14px;
        top: -150px;
        opacity: 0;
        visibility: hidden;
        transition: .3s;
        button svg {
            width: 24px;
            height: 24px;
            flex-shrink: 0;
        }

        &.active {
            opacity: 1;
            visibility: visible;
        }
    }

    &__plus svg {
        width: 21px;
        height: 21px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__plus:hover svg {
        opacity: 0.8;
    }

    &__up svg {
        width: 22px;
        height: 22px;
        flex-shrink: 0;
        transition: .3s;
    }

    &__up:hover svg {
        opacity: 0.8;
    }


    &__middle {
        margin-bottom: 200px;
    }

    &__dialog {
        display: flex;
        flex-direction: column;
        gap: 27px;
    }

    .phone__dialog-item .phone__user {
        background: transparent;
    }

    .phone__dialog-item .phone__user p {
        
        font-weight: 590;
        font-style: Semibold;
        font-size: 17px;
        leading-trim: NONE;
        line-height: 22px;
        letter-spacing: -0.4px;
        color: rgba(0, 0, 0, 1);
        margin: 0;
    }

    &__dialog-text {
        
        font-weight: 400;
        font-size: 17px;
        leading-trim: NONE;
        line-height: 27px;
        letter-spacing: -0.1px;
        color: rgba(0, 0, 0, 1);
        margin: 0;
        padding-left: 38px;
    }
}

.header {
    background: transparent;

    @include vp-767 {
        background: transparent;
    }

    &.is-open {
        .phone__menu {
            transform: translateX(0);
        }
    }
}