.step2 {
    &__wrapper {
        
        width: 100%;
        margin: 0 auto;
        display: block;
        padding: 70px 0;
    }

    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 40px;
        leading-trim: CAP_HEIGHT;
        line-height: 62.5px;
        letter-spacing: 0px;
        text-align: center;
        vertical-align: middle;
        margin: 0 0 30px;
        color: rgba(255, 255, 255, 1);
    }

    &__icon {
        width: 56px;
        height: 56px;
        margin: 0 auto 20px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__form {
        overflow: hidden;
        background: rgba(180, 209, 235, 1);
        max-width: 460px;
        width: 100%;
        border-radius: 30px;
        margin: 0 auto;
        padding: 30px;
    }

    &__text {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 40px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 30px;
    }

    &__quest {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 24px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        margin: 0 0 30px;
        color: rgba(58, 58, 58, 1);
    }

    &__top {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    .custom-input {
        background: rgba(254, 255, 255, 1);
        padding: 12px 45px 12px 20px;
        border-radius: 30px;
        position: relative;
    }

    .custom-input input {
        padding: 0;
        color: black;
    }

    .custom-input__toggle.js-toggle-password {
        position: absolute;
        right: -25px;
        top: 50%;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        cursor: pointer;
    }

    .custom-checkbox {
        margin-bottom: 15px;
    }

    .custom-checkbox__icon {
        position: relative;
        /* ваши текущие стили */
    }
      
    .custom-checkbox__icon::after {
        content: "";
        position: absolute;
        top: 3px;
        left: 6px;
        width: 6px;
        height: 12px;
        border: solid #000; /* цвет галочки */
        border-width: 0 2px 2px 0;
        opacity: 0;
        transform: rotate(45deg);
        transition: opacity 0.3s ease;
    }
      
    input:checked + .custom-checkbox__icon::after {
        opacity: 1;
    }

    &__btn {
        max-width: 100%;
        width: 100%;
        border-radius: 60px;
        background: rgba(0, 0, 0, 1);
        padding: 13px;
        color: rgba(255, 255, 255, 1);
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 100%;
        letter-spacing: 0%;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        gap: 10px;
        transition: .3s;
    }

    &__btn:hover {
        opacity: 0.8;
    }

    &__cards {
        max-width: 1100px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        align-items: start;
        margin: 0 auto;
        gap: 20px;

        @media screen and (max-width: 900px) {
            grid-template-columns: 1fr 1fr;
        }

        @media screen and (max-width: 600px) {
            grid-template-columns: 1fr;
        }
    }

    &__card {
        border: 1px solid rgba(107, 161, 218, 1);
        border-radius: 30px;
        background: rgba(180, 209, 235, 1);
        padding: 20px;

    }

    &__name {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 15px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        color: rgba(49, 119, 188, 1);
        margin: 0 0 15px;
    }

    &__desc {
    
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 25px;
        color: rgba(49, 119, 188, 1);
    }

    &__price {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 28px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(29, 29, 31, 1);
        margin: 0 0 25px;
    }

    &__what {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 25px;
    }

    &__card {
        ul {
            list-style: disc;
            display: flex;
            flex-direction: column;
            gap: 11px;
            padding-left: 20px;
            margin-bottom: 25px;
        }

        li {
            list-style: disc;
            
            font-weight: 500;
            font-style: Medium;
            font-size: 16px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
        }

        span {
            
            font-weight: 500;
            font-style: Italic;
            font-size: 14px;
            leading-trim: CAP_HEIGHT;
            line-height: 130%;
            letter-spacing: 0px;
            text-align: center;
            color: rgba(58, 58, 58, 1);
            display: block;
            margin: 0 auto 15px;
        }
    }

    &__card {
        &--white {
            background: rgba(254, 255, 255, 1);
            position: relative;
            border: none;
            .step2__image {
                position: absolute;
                width: 83px;
                height: 80px;
                top: -20px;
                right: -20px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }


}