.tarif2 {
    overflow-x: hidden;
    overflow-y: auto;
    min-height: 100vh;
    
    &__wrapper {
        display: flex;
        min-height: 100vh;
        

        @include vp-767 {
            height: 100%;
        }
    }

    &__left {
        
        width: 40%;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 30px 30px 0;
        min-height: 100vh;
        
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: -100vw;
            width: calc(100% + 100vw);
            height: 100%;
            background: rgba(107, 161, 218, .55);
            z-index: -1;
        }

        @media screen and (max-width: 500px) {
            padding-right: 10px;
        }

    }

    &__left-content {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    &__label {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 100%;
        letter-spacing: 0px;
        vertical-align: middle;
        color: rgba(255, 255, 255, 1);
        margin: 0 0 10px;

        @media screen and (max-width: 500px) {
            font-size: 14px;
        }
    }

    &__links-left {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-bottom: 10px;
    }

    &__link-left {
        
        font-weight: 400;
        font-style: Regular;
        font-size: 16px;
        leading-trim: NONE;
        line-height: 110.00000000000001%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);

        &--active {
            font-weight: 900;
        }

        @include vp-389 {
            font-size: 12px;
        }
    }

    &__links-left-bottom {
        display: flex;
        flex-direction: column;
        gap: 10px;
        margin-top: auto;
    } 

    

    &__right {
        width: 70%;
        padding: 30px 0 30px 120px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 1130px) {
            padding: 30px 0 30px 30px;
        }

        @include vp-389 {
            padding: 30px 0 30px 20px;
        }
    }

    &__top {
        position: relative; 
        padding-bottom: 20px;
        display: flex;
        justify-content: end;

        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: -120px;
            width: 100vw;
            height: 1px;
            background: rgba(107, 161, 218, 1);
            transform: translateX(calc(-50vw + 50%)); 

            @media screen and (max-width: 1130px) {
                left: -30px;
            }

            
            @include vp-389 {
                left: -20px;
            }
        }
    }

    &__icon {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__user {
        svg {
            width: 31px;
            height: 32px;
        }

        &--text {
            
            font-weight: 400;
            font-style: Regular;
            font-size: 18px;
            leading-trim: NONE;
            line-height: 110.00000000000001%;
            letter-spacing: 0px;
            color: rgba(58, 58, 58, 1);
            margin-left: 10px;
        }
    }

    &__bottom {
        margin-top: 30px;
    }

    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 25px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        color: rgba(29, 29, 31, 1);
        margin: 0 0 30px;
    }

    &__text {
        
        font-weight: 800;
        font-style: ExtraBold;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 20px;      
        
        span {
            color: rgba(255, 64, 38, 1);
        }
    }

    &__text-small {
        
        font-weight: 600;
        font-style: Semibold;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        margin: 0;
        padding-top: 10px;
    }

   
}