.sms {
    &__wrapper {
        max-width: 400px;
        width: 100%;
        margin: 0 auto;
        display: block;
        padding: 190px 0;
    }
  
    &__title {
        
        font-weight: 700;
        font-style: Bold;
        font-size: 24px;
        leading-trim: CAP_HEIGHT;
        line-height: 120%;
        letter-spacing: 0%;
        text-align: center;
        margin: 0 0 20px;        
    
    }
  
    &__description {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        color: rgba(58, 58, 58, 1);
        margin: 0 0 30px;        
    }
  
    &__form {
      display: flex;
      flex-direction: column;
    }
  
    &__fields {
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 20px;
      background: rgba(254, 255, 255, 1);
      border-radius: 20px;
      padding: 20px;
      margin-bottom: 30px;
    }

    
    &__dots {
        display: flex;
        justify-content: center;
        gap: 5px;
        pointer-events: none; 
    }
    
    &__dot {
    
        width: 6px;
        height: 6px;
        background: rgba(143, 140, 140, 1);
        border-radius: 50%;
        position: relative;
    }

    &__text {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 16px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        color: rgba(143, 140, 140, 1);
        margin: 0;
    }

  
    &__timer {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        margin: 0 0 15px;        
    }
  
    &__buttons {
      display: flex;
      flex-direction: column;
      gap: 15px;
    }
  
    &__button {
        
        font-weight: 500;
        font-style: Medium;
        font-size: 18px;
        leading-trim: CAP_HEIGHT;
        line-height: 130%;
        letter-spacing: 0px;
        text-align: center;
        text-decoration: underline;
        text-decoration-style: solid;
        text-decoration-offset: 0%;
        text-decoration-thickness: 0%;
        color: rgba(255, 255, 255, 1);    
        transition: .3s;    
    }

    &__button:hover {
        opacity: 0.8;
    }
}